import React, { useEffect, useReducer } from "react";
import { ThemeProvider } from "@mui/material/styles";
import Styles from "../Styles";
import "../App.css";
import { Button, Autocomplete, TextField } from "@mui/material";
import theme from "../theme";

//An example of a React Functional Component using JSX syntax
const AddAdvisory = (props) => {
  //State variables
  const initialState = {
    alerts: [],
    countries: [],
    btnStatus: true,
    advisor: "",
    country: "",
  };
  //local
  //const SERVERURL = "http://localhost:5000/api";
  //production
  const SERVERURL = "/api";
  //when the option is clicked from the autoComplete
  const onChange = (e, selectedOption) => {
    selectedOption === "" ||
    selectedOption === null ||
    selectedOption === undefined ||
    state.advisor === ""
      ? setState({
          country: selectedOption,
          btnStatus: true,
        })
      : setState({
          country: `${selectedOption}`,
          btnStatus: false,
        });
  };

  //update the state to a newState using the spread opeartor and the rest operator
  //In here, we are taking the old state(using spread operator), updating
  //some values and then copying them to a new state( using the rest operator)
  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);

  //runs when component when mounted
  useEffect(() => {
    fetchAlerts();
  }, []);

  const fetchAlerts = async () => {
    try {
      props.dataFromChild(`Attempting to load alerts from server...`);
      let response = await fetch(`${SERVERURL}/alerts`, {
        method: "GET",
      });

      let json = await response.json();

      // get the message based on countr
      setState({
        alerts: json.alerts,
        countries: json.alerts.map((a) => a.name),
      });

      props.dataFromChild(`${json.alerts.length} countries loaded`);
    } catch (error) {
      props.dataFromChild(`${error.message}`);
    }
  }; //End of the fetchUser function

  //adds the advisory
  const addAdvisory = async () => {
    //advisory state variables
    let advisory = {
      name: state.advisor,
      country: state.country,
      date: "",
      message: "",
    };

    try {
      //get alert info from the country name.
      // eslint-disable-next-line array-callback-return
      let currentAdvisory = state.alerts.find(
        (element) => element.name === state.country
      );

      var today = new Date();
      var date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      var time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      var dateTime = date + " " + time;

      //set the message and date values
      advisory.message = currentAdvisory.text;
      advisory.date = dateTime;

      let myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");

      //creating advisory data object.
      const data = {
        name: advisory.name,
        country: advisory.country,
        text: advisory.message,
        date: advisory.date,
      };
      //create json string
      const myJson = JSON.stringify(data);

      //submitting the post request with advisory data.
      let response = await fetch(`${SERVERURL}/alerts/addadvisory`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
        body: myJson,
      });
      //snackbar of successful addition
      props.dataFromChild(`Advisory added on ${advisory.date}`);
    } catch (error) {
      props.dataFromChild(`${error.message} - added advisory`);
    }
  };

  //sets the input value
  const handleNameInput = (text) => {
    text.target.value === "" ||
    text.target.value === null ||
    text.target.value === undefined ||
    state.country === ""
      ? setState({ advisor: text.target.value, btnStatus: true })
      : setState({ btnStatus: false, advisor: text.target.value });
  };

  //get all the countries and add in the autocomplete
  return (
    <ThemeProvider theme={theme}>
      <h3 style={Styles.Text}>Add Advisory</h3>
      <TextField
        style={{ marginBottom: "1%", marginLeft: "10%", width: "80%" }}
        placeholder="Enter Advisor Name"
        onChange={handleNameInput}
        value={state.advisor}
      />

      <Autocomplete
        data-testid="autocomplete"
        options={state.countries}
        getOptionLabel={(option) => option}
        style={{ width: "80%", marginLeft: "10%" }}
        onChange={onChange}
        renderInput={(params) => (
          <TextField
            {...params}
            label="available countries"
            variant="outlined"
            fullWidth
          />
        )}
      />
      <Button
        variant="contained"
        style={Styles.advisoryBtn}
        disabled={state.btnStatus}
        onClick={addAdvisory}
      >
        ADD ADVISORY
      </Button>
    </ThemeProvider>
  );
};
export default AddAdvisory;
