import React, { useEffect, useReducer } from "react";
import { ThemeProvider } from "@mui/material/styles";
import "../App.css";
import Styles from "../Styles";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import {
  Autocomplete,
  TextField,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
} from "@mui/material";
import theme from "../theme";

//An example of a React Functional Component using JSX syntax
const ListAdvisories = (props) => {
  //local
  //const SERVERURL = "http://localhost:5000/api";
  //production
  const SERVERURL = "/api";

  const initialState = {
    advisor: "",
    country: "",
    advisory: [], //advisory is list of travellers or region or sub-region
    countriesAlert: [],
    radioElement: "Traveller",
    hideTable: true,
    optionValue: "",
  };

  //update the state to a newState using the spread opeartor and the rest operator
  //In here, we are taking the old state(using spread operator), updating
  //some values and then copying them to a new state( using the rest operator)
  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);

  //runs when component is mounted
  useEffect(() => {
    loadTravellers();
  }, []);

  const onChange = (e, selectedOption) => {
    //if selected value who look for the alert
    //selected option is the one that is selected in the radio button.
    setState({ optionValue: selectedOption, countriesAlert: [] });
    if (
      selectedOption !== "" &&
      selectedOption !== null &&
      selectedOption !== undefined
    ) {
      switch (state.radioElement) {
        case "Region":
          return displayRegionAlert(selectedOption);
        case "Sub-Region":
          return displaySubRegionAlert(selectedOption);
        default:
          return displayTravellersAlert(selectedOption);
      }
    } else {
      setState({ hideTable: true });
    }
  };

  //If radioButton is changed
  const handleChange = (event) => {
    setState({
      hideTable: true,
      countriesAlert: [],
      advisory: [],
      radioElement: event.target.value,
      optionValue: ``,
    });
    switch (event.target.value) {
      case "Region":
        return loadRegion();
      case "Sub-Region":
        return loadSubRegion();
      default:
        return loadTravellers();
    }

    //after we load
  };

  //displays country and the alert info based on region
  const displayRegionAlert = async (selectedOption) => {
    console.log(selectedOption);

    try {
      let response = await fetch(
        `${SERVERURL}/alerts/region/${selectedOption}`,
        {
          method: "GET",
        }
      );

      //get the advisory list of the person selected.
      let json = await response.json();

      console.log(json);

      // gets the region which is not empty
      setState({
        countriesAlert: json.alertforregion,
      });

      setState({ hideTable: false });
    } catch (error) {
      props.dataFromChild(`${error.message}`);
    }
  };
  //loads sub-Region
  //displays country and the alert info based on sub-region
  const displaySubRegionAlert = async (selectedOption) => {
    console.log(selectedOption);

    try {
      let response = await fetch(
        `${SERVERURL}/alerts/subregion/${selectedOption}`,
        {
          method: "GET",
        }
      );

      //get the advisory list of the person selected.
      let json = await response.json();

      // gets the region which is not empty
      setState({
        countriesAlert: json.alertsforsubregion,
      });

      setState({ hideTable: false });
    } catch (error) {
      props.dataFromChild(`${error.message}`);
    }
  };
  //loads travellers
  //displays country and the alert info based on user
  const displayTravellersAlert = async (selectedOption) => {
    console.log(selectedOption);

    try {
      let response = await fetch(
        `${SERVERURL}/alerts/travellerAdvisory/${selectedOption}`,
        {
          method: "GET",
        }
      );

      //get the advisory list of the person selected.
      let json = await response.json();

      // gets the region which is not empty
      setState({
        countriesAlert: json.alertsforTravellers,
      });

      setState({ hideTable: false });
    } catch (error) {
      props.dataFromChild(`${error.message}`);
    }
  };

  //loads Region
  const loadRegion = async () => {
    try {
      let response = await fetch(`${SERVERURL}/alerts`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
      });

      let json = await response.json();
      console.log(json);
      // gets the region which is not empty
      let obj = json.alerts
        .map((a) => a.region)
        .filter((value, index, self) => self.indexOf(value) === index);

      setState({
        advisory: obj,
      });

      props.dataFromChild(`${obj.length} region loaded`);
    } catch (error) {
      props.dataFromChild(`${error.message}`);
    }
  };
  //loads sub-Region
  const loadSubRegion = async () => {
    try {
      let response = await fetch(`${SERVERURL}/alerts`, {
        method: "GET",
      });

      let json = await response.json();

      // gets the region which is not empty
      //get unique list of subregion from alert list
      let obj = json.alerts
        .map((a) => a.subregion)
        .filter((value, index, self) => self.indexOf(value) === index);
      setState({
        advisory: obj,
      });

      props.dataFromChild(`${obj.length} sub-region loaded`);
    } catch (error) {
      props.dataFromChild(`${error.message}`);
    }
  };
  //loads travellers
  const loadTravellers = async () => {
    try {
      let response = await fetch(`${SERVERURL}/alerts/travellerAdvisory`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json; charset=utf-8",
        },
      });

      let json = await response.json();
      let obj = json.travellerAdvisory
        .map((a) => a.name)
        .filter((value, index, self) => self.indexOf(value) === index);
      setState({
        advisory: obj,
      });

      // gets the region which is not empty
      setState({
        advisory: obj,
      });

      props.dataFromChild(`${obj.length} travellers loaded`);
    } catch (error) {
      props.dataFromChild(`${error.message}`);
    }
  };

  //Traveler Region subregion
  return (
    <ThemeProvider theme={theme}>
      <h3 style={{ textAlign: "center" }}>List Advisories By:</h3>
      <div style={{ textAlign: "center", justifyItems: "center" }}>
        <FormControl style={{ alignItems: "center" }}>
          <RadioGroup row value={state.radioElement} onChange={handleChange}>
            <FormControlLabel
              value="Traveller"
              control={<Radio />}
              label="Traveller"
            />
            <FormControlLabel
              value="Region"
              control={<Radio />}
              label="Region"
            />
            <FormControlLabel
              value="Sub-Region"
              control={<Radio />}
              label="Sub-Region"
            />
          </RadioGroup>
        </FormControl>

        <Autocomplete
          data-testid="autocomplete"
          options={state.advisory}
          getOptionLabel={(option) => option}
          style={{ width: "80%", marginLeft: "10%" }}
          onChange={onChange}
          value={state.optionValue}
          renderInput={(params) => (
            <TextField
              {...params}
              placeholder={state.radioElement}
              variant="outlined"
              fullWidth
            />
          )}
        />

        <TableContainer
          hidden={state.hideTable}
          className="center"
          style={{
            width: "80%",
            marginLeft: "10%",
            overflow: "scroll",
            height: "200px",
            marginTop: "10px",
          }}
        >
          <Table size="medium">
            <TableHead>
              <TableRow>
                <TableCell style={Styles.tableHead}>Country</TableCell>
                <TableCell style={Styles.tableHead} align="left">
                  Alert Information
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {state.countriesAlert.map((row, index) => (
                <TableRow key={index}>
                  <TableCell>
                    {row.country.length === 2 ? row.name : row.country}
                  </TableCell>
                  <TableCell align="left">{`${row.text} ${row.date}`}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </ThemeProvider>
  );
};

export default ListAdvisories;
