import React, { useReducer } from "react";
import { Route, Link, Routes } from "react-router-dom";
import MenuIcon from "@mui/icons-material/Menu";
import { ThemeProvider } from "@mui/material/styles";
import theme from "../theme";
import HomeComponent from "./HomeComponent";
import AddAdvisoryComponent from "./AddAdvisoryComponent";
import ListAdvisories from "./ListAdvisoriesComponent";
import styles from "../Styles";
import logo from "../images/alertImg.jpg";
import {
  Toolbar,
  AppBar,
  Menu,
  MenuItem,
  IconButton,
  Typography,
  Snackbar,
} from "@mui/material";

function App() {
  const initialState = {
    snackbarMsg: "",
    msgFromParent: "data from parent",
    gotData: false,
    anchorEl: null,
  };

  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);

  const snackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    //change the state
    setState({ gotData: false });
  };

  const msgFromChild = (msg) => {
    setState({ snackbarMsg: msg, gotData: true });
  };

  const handleClose = () => {
    //sets to null
    setState({ anchorEl: null });
  };

  const handleClick = (event) => {
    setState({ anchorEl: event.currentTarget });
  };

  return (
    <ThemeProvider theme={theme}>
      <AppBar position="relative">
        <Toolbar>
          <Typography variant="h6" color="inherit">
            Travel Advisor
          </Typography>
          <IconButton
            onClick={handleClick}
            color="inherit"
            style={{ marginLeft: "auto", paddingRight: "1vh" }}
          >
            <MenuIcon />
          </IconButton>

          <Menu
            id="simple-menu"
            anchorEl={state.anchorEl}
            open={Boolean(state.anchorEl)}
            onClose={handleClose}
          >
            <MenuItem onClick={handleClose} component={Link} to="/home">
              Home
            </MenuItem>
            <MenuItem onClick={handleClose} component={Link} to="/AddAdvisory">
              AddAdvisory
            </MenuItem>
            <MenuItem
              onClick={handleClose}
              component={Link}
              to="/ListAdvisories"
            >
              ListAdvisories
            </MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>

      <div>
        <div style={{ textAlign: "center" }}>
          <img src={logo} style={styles.image} alt="logo"></img>
          <h2>World Wide Travel Alerts</h2>
        </div>
        <Routes>
          <Route
            path="/home"
            element={<HomeComponent dataFromChild={msgFromChild} />}
          />
          <Route
            path="/AddAdvisory"
            element={<AddAdvisoryComponent dataFromChild={msgFromChild} />}
          />
          <Route
            path="/ListAdvisories"
            element={<ListAdvisories dataFromChild={msgFromChild} />}
          />
        </Routes>

        <Snackbar
          open={state.gotData}
          message={state.snackbarMsg}
          autoHideDuration={3000}
          onClose={snackbarClose}
        />
      </div>
    </ThemeProvider>
  );
}

export default App;
